import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import {
  FaEnvelope,
  FaExchangeAlt,
  FaLock,
  FaPlus,
  FaRemoveFormat,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";

import { ImListNumbered } from "react-icons/im";

import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./GroupEdit.css";

import { RoutesString } from "pages/routesString";
import groupAPI from "api/common/group";
import typeOfKnowledgeAPI from "api/common/type-of-knowledge";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import { number } from "yup/lib/locale";
import i18n from "i18n/i18n";
import { isErrors } from "utils/utils";

export default function GroupEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    idLKKT: 0,
    // maGN: "",
    tieuDe: "",
    stt: "",
    loaiNhom: "BB",
    soTCBB: "",
    // isDeleted: false,
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const loaiNhom = [
    {
      value: "BB",
      name: t("admin.group.OBLIGATE"),
    },
    {
      value: "TC",
      name: t("admin.group.ELECTIVE"),
    },
  ];
  const [tOfKl, setTOfKl] = useState([]);
  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
    try {
      const tOfKlRes = await typeOfKnowledgeAPI.getAll({limit: -1});
      setTOfKl(tOfKlRes.contents);
      // eslint-disable-next-line
    } catch (err) {
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await groupAPI.getById(id);
      setInitialValues({
        idLKKT: res.idLKKT !== undefined && res.idLKKT.toString(),
        // maGN: res.maGN,
        tieuDe: res.tieuDe,
        stt: res.stt,
        loaiNhom: res.loaiNhom,
        soTCBB: res.soTCBB,
        // isDeleted: res.isDeleted,
      });
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values) {
    values.idLKKT = parseInt(values.idLKKT, 10);
    if (isUpdatePage) {
      const { isDeleted, ...body } = values;
      try {
        const res = await groupAPI.putById(id, body);
        if (res.status === 200) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const { isDeleted, ...body } = values;
      try {
        const res = await groupAPI.post(body);
        if (res.id) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.ADD_SUCCESS")],
          });
          history.push(`${RoutesString.ADMIN_GROUP_EDIT}?id=${res.id}`);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await groupAPI.deleteById(id);
      if (res.status === 200) {
        setNotification({
          color: "success",
          isOpen: true,
          messages: [t("admin.common.DELETED")],
        });
        updateForm(id);
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.group.UPDATE_GROUP")
            : t("admin.group.ADD_GROUP")
        }
        items={[
          {
            href: RoutesString.ADMIN_GROUP_LIST,
            title: t("admin.group.DETAIL_GROUP"),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t("admin.group.UPDATE_GROUP")
            : t("admin.group.ADD_GROUP")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            // maGN: Yup.number().required(i18n.t("REQUIRED_FIELD_WARNING")),
            // tieuDe: Yup.string(),
            stt: Yup.number().required(i18n.t("REQUIRED_FIELD_WARNING")).min(0, i18n.t("admin.POSITIVE_WARNING")),
            soTCBB: Yup.number()
              .required(i18n.t("REQUIRED_FIELD_WARNING"))
              .min(0, i18n.t("admin.POSITIVE_WARNING")),
          })}
        >
          {({ values, errors, handleChange, handleReset }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              {/* <FormGroup className="row">
                <Label for="maGN" className="col-lg-3">
                  Code <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <ImListNumbered />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="maGN"
                      value={values.maGN}
                      onChange={handleChange}
                      invalid={errors.maGN}
                      readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.maGN && (
                    <FormText color="danger">{errors.maGN}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br /> */}
              <FormGroup className="row">
                <Label for="tieuDe" className="col-lg-3">
                  {t("admin.group.TITLE")}
                  {/* <AsteriskIcon /> */}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUserAlt />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="tieuDe"
                      value={values.tieuDe}
                      onChange={handleChange}
                    // invalid={errors.tieuDe}
                    // readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.tieuDe && (
                    <FormText color="danger">{errors.tieuDe}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="stt" className="col-lg-3">
                  {t("admin.group.ORDINAL_NUMBERS")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <ImListNumbered />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="stt"
                      value={values.stt}
                      onChange={handleChange}
                    // invalid={errors.stt}
                    // readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.stt && (
                    <FormText color="danger">{errors.stt}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="soTCBB" className="col-lg-3">
                  {t("admin.group.REQUIRED_CREDITS")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <ImListNumbered />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="soTCBB"
                      value={values.soTCBB}
                      onChange={handleChange}
                    // invalid={errors.soTCBB}
                    // readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.soTCBB && (
                    <FormText color="danger">{errors.soTCBB}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="loaiNhom" className="col-lg-3">
                  {t("admin.group.TYPE")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaLock />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="loaiNhom"
                      value={values.loaiNhom}
                      onChange={handleChange}
                    >
                      {loaiNhom &&
                        loaiNhom.map((item, index) => {
                          if (index === 0) {
                            return (
                              <option key={index} value={item.value} selected>
                                {item.name}
                              </option>
                            );
                          }
                          return (
                            <option key={index} value={item.value}>
                              {item.name}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="idLKKT" className="col-lg-3">
                  {t("admin.group.TYPE_OF_KNOWLEDGE")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idLKKT"
                      value={values.idLKKT}
                      onChange={handleChange}
                    >
                      {tOfKl &&
                        tOfKl.map((item, index) => {
                          return (
                            <option key={index} value={item.id.toString()}>
                              {`${item?.khoiKienThuc?.chiTietNganh?.nganhDaoTao?.maNganhDaoTao}-${item?.khoiKienThuc?.chiTietNganh?.khoa}-${item?.khoiKienThuc?.ten}-${item?.ten}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              {/* {isUpdatePage && (
                <>
                  <FormGroup className="row">
                    <Label for="isDeleted" className="col-lg-3">
                      {t("admin.common.AVAILABLE_STATUS")}
                    </Label>
                    <div className="col-lg-6">
                      <InputGroup>
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <FaRemoveFormat />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="select"
                          name="isDeleted"
                          value={values.isDeleted}
                          onChange={handleChange}
                        >
                          {availableStatus &&
                            availableStatus.map((item, index) => {
                              return (
                                <option key={index} value={item.value}>
                                  {item.name}
                                </option>
                              );
                            })}
                        </Input>
                      </InputGroup>
                    </div>
                  </FormGroup>{" "}
                  <br />
                </>
              )} */}
              <FormGroup>
                <Button
                  color="success"
                  disabled={isErrors(errors)}
                  onClick={() => handleEditOnClick(values)}
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
